body{
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 1.2em;
  margin: 0 auto;
  max-width: 85vw;
  background: linear-gradient(
    to left,
    rgba(140, 140, 140, 1) 0%,
    rgba(200, 200, 200, 1) 100%
  );
}

h1{
  text-align: center;
}

#inputgrid{
  margin: 30px 0;
  display: flex;
  justify-content: center;
  grid-template-columns: 1fr 2fr;
  grid-gap: 10px;
  height: 10vw;
  max-height: 2em;
}

input[type=number]{
  text-align: center;
  font-size: 1em;
  width: 12em;
  height: 2.25em;
  box-sizing: border-box;
  border: 3px solid rgba(40,40,40);
}

input[type=text]:focus {
  border: 3px solid rgba(40,40,40);
}

#gamestart{
  border: 3px solid rgba(40,40,40);
  color: rgba(225,225,225);
  background-color:rgba(40,40,40);
  font-size: 1.5em;
  width: 6em;
  height: 1.5em
}

#gamestart:hover{
  color: rgba(40,40,40);
  background-color: rgba(225,225,225);
  cursor: pointer;
}
